import React from 'react';
import { useEffect } from 'react';
import { useState } from 'react';
import MyButton from '../../components/MyButton/MyButton';
import MyFileLoader from '../../components/MyFileLoader/MyFileLoader';
import Navbar from '../../components/Navbar/Navbar';
import $api from '../../http';
import MyInput from '../Auth/components/MyInput/MyInput';
import cl from './CreateOffer.module.sass'

const CreateOffer = () => {
  const [offer, setOffer] = useState({
    creator: localStorage.getItem('id')
  })
  const [status, setStatus] = useState(false);
  const [img, setImg] = useState([])
  const changeOffer = (e) => {
    ['rooms', 'guests', 'lowestPrice', 'phone'].includes(e.target.name) ? (
      setOffer({ ...offer, [e.target.name]: Number(e.target.value) })
    ) : (
      setOffer({ ...offer, [e.target.name]: e.target.value })
    )
  }

  const createOffer = async () => {
    console.log(img)
    console.log('length', img.length)
    const data = new FormData();
    for (let i = 0; i < img.length; i++) {
      data.append('images', img[i]);
    }
    data.append('offer', JSON.stringify(offer))
    const res = await $api.post('/createOffer', data) || 1
    if (res.status === 200) setStatus(true)
  }

  const previewPhoto = (e) => {
    console.log('e.target.files', e.target.files);
    setImg([...img, ...e.target.files])

    for (let i = 0; i < e.target.files.length; i++) {
      let reader = new FileReader();

      reader.readAsDataURL(e.target.files[i]);

      reader.onload = () => {
        let newImg = document.createElement('img');
        newImg.onclick = () => {
          const modal = document.querySelector(`.${cl.modal}`)
          modal.style.display = 'flex'
          modal.onclick = () => modal.style.display = 'none'
          document.querySelector(`#${cl.prev}`).src = `${newImg.src}`
        }
        newImg.src = reader.result;
        document.querySelector(`.${cl.preview}`).appendChild(newImg)
      }
    }
    e.target.value = ''
  }

  useEffect(() => {
    setOffer({ ...offer, offerType: '' })
  }, [])

  console.log('offer', offer);

  return (
    <>
      <Navbar />
      <div className='wrapper'>
        <div className={cl.createOffer}>
          <h1> </h1>
          <label htmlFor="types"> ?</label>
          <select name="offerType" id="types" defaultValue={""} onChange={(e) => changeOffer(e)}>
            <option value=""></option>
            <option value=" "> </option>
            <option value=""></option>
            <option value=""></option>
          </select>
          {offer.offerType == '' ?
            <>
              <label htmlFor="allocationType"> ?</label>
              <select name="allocationType" id="types" defaultValue={""} onChange={(e) => changeOffer(e)}>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
              </select>
            </>
            :
            null
          }
          <MyInput type='text' placeholder='' name='offerName' onChange={(e) => changeOffer(e)} />
          <MyInput type='text' placeholder='' name='country' onChange={(e) => changeOffer(e)} />
          <MyInput type='text' placeholder='' name='city' onChange={(e) => changeOffer(e)} />
          <MyInput type='text' placeholder='' name='adress' onChange={(e) => changeOffer(e)} />
          <div className={cl.quantity}>
            <MyInput className={cl.amount} type='text' name='rooms' placeholder='- ' onChange={(e) => changeOffer(e)} />
            <MyInput className={cl.amount} type='text' name='guests' placeholder='- ' onChange={(e) => changeOffer(e)} />
          </div>
          <MyInput type='text' name='lowestPrice' placeholder=' ' onChange={(e) => changeOffer(e)} />
          <MyInput type='text' name='phone' placeholder='' onChange={(e) => changeOffer(e)} />
          <MyInput type='text' name='description' placeholder='' onChange={(e) => changeOffer(e)} />
          <MyInput type='text' name='highlights' placeholder='   ' onChange={(e) => changeOffer(e)} />
          <label htmlFor='file'> </label>
          <MyFileLoader name='file' className={cl.file} multiple onChange={(e) => previewPhoto(e)} img={img.length} />
          <div className={cl.preview}>
            <div className={cl.modal}>
              <img id={cl.prev} src="#" alt="" />
            </div>
          </div>
          {status ?
            <h1>  !</h1> : null
          }
          <MyButton onClick={() => { createOffer(); console.log('offer', offer) }}> </MyButton>
        </div>
      </div>
    </>
  );
}

export default CreateOffer;
