import React from 'react';
import MyButton from '../../../../components/MyButton/MyButton';
import MyInput from '../../../Auth/components/MyInput/MyInput';
import cl from './OfferSearchBar.module.sass'

const OfferSearchBar = (props) => {
  return (
    <div className={`${cl.search} ${props.className}`}>
      <MyInput className={cl.input} name='city' type='text' placeholder='   ?'></MyInput>
      <MyInput className={cl.input} name='guests' type='text' placeholder='- '></MyInput>
      <MyInput className={cl.input} name='rooms' type='text' placeholder='- '></MyInput>
      <MyButton className={cl.button}> </MyButton>
    </div>
  );
}

export default OfferSearchBar;
